.nav-container {
    background-color: #282828;
    width: 100%;
    color: #f1f1f1;
    display: grid;
    grid-template-columns: repeat(3, 100px);
    grid-template-rows: 40px;
    justify-content: right;
}
.nav-item {
    display: flex;
    background-color: #282828;
    justify-content: center;
    align-items:center;
    width: 100%;
    transition: background-color 0.25s;
    cursor: pointer;
}
.nav-item:hover {
    background-color: #585858;
    
}